import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";

const MediaCard = (props) => {
	const projects = [
		{
			key: 1,
			title: "App.io",
			github: "https://github.com/juggernautSmash/app.io.git",
			heroku: "https://still-wave-39576.herokuapp.com/",
			description: "Project 3: A more complicated to-do list",
		},
		{
			key: 2,
			title: "The Wine Cellar",
			github: "https://github.com/Bryantkrause/TheWineCellar.git",
			heroku: "https://vast-tor-55162.herokuapp.com/",
			description: "Project 2: This is a wine storage application",
		},
		{
			key: 3,
			title: "SuperDuperAwesomeFridgeRaidingFoodinator",
			github: "https://github.com/Bryantkrause/TheWineCellar.git",
			description:
				"Project 1: for Bootcamp Spot. This is an app that fetches recipes based on user input ingredients.",
		},
	];
	return (
		console.log(projects),
		(
			<Grid style={{ margin: "1%" }}>
				{projects.map((project) => (
					<Card>
						<CardActionArea>
							<CardMedia
								// image="/static/images/cards/contemplative-reptile.jpg"
								title={project.title}
							/>
							<CardContent>
								<Typography gutterBottom variant="h5" component="h2">
									{project.title}
								</Typography>
								<Typography variant="body2" color="textSecondary" component="p">
									{project.description}
								</Typography>
							</CardContent>
						</CardActionArea>
						<CardActions>
							<Button size="small" color="primary">
								{project.github}
							</Button>
							<Button size="small" color="primary">
								Learn More
							</Button>
						</CardActions>
					</Card>
				))}
			</Grid>
		)
	);
};

export default MediaCard;
